@import "../../assets/styles/vars.scss";

.page-section-title {
  font-weight: 500;
  font-size: var(--font-size-l);
  line-height: rem(20);
  color: var(--color-content-high);
  margin: 1rem 0 1rem 0;
}

.company-fist-group_row {
  margin: 0 -1.5rem;
  .col {
    padding: 0 1.5rem;
    border-bottom: 1px solid var(--color-background-low);
    padding-bottom: rem(8);
  }
}

.company-second-group {
  border-bottom: 1px solid var(--color-background-low);
  padding-bottom: 1rem;
  margin-bottom: 1rem;
}

.paginator-links {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: rem(32);
}

.paginator-link {
  display: flex;
  align-items: center;
  display: inline-flex;
  border-radius: 100%;
  font-size: var(--font-size-m);
  cursor: pointer;
  font-weight: 500;
  .icon {
    font-size: rem(24);
    color: var(--color-content-mid);
  }
  &:hover {
    text-decoration: none;
  }
}
.paginator-link--disabled {
  opacity: 0.5;
  pointer-events: none;
}

@media (max-width: rem(640)) {
  .col.col_company-score {
    padding-bottom: 1rem;
  }
}

@media (min-width: rem(640)) {
  .company-fist-group_row .col {
    padding-bottom: 1.5rem;
  }
  .company-fist-group_row {
    display: flex;
    flex-wrap: wrap;
  }
  .col_company-info {
    border-bottom: 1px solid var(--color-background-low);
  }
  .col_company-financials {
    border-right: 1px solid var(--color-background-low);
  }
  .col_company-financials,
  .col_company-score {
    width: 50%;
  }
  .company-second-group_row {
    display: flex;
    flex-wrap: wrap;
    .col {
      width: 50%;
    }
  }
}

@media (min-width: rem(1024)) {
  .company-fist-group {
    border-bottom: 1px solid var(--color-background-low);
  }
  .company-fist-group_row {
    margin: 0 -0.75rem;
    .col {
      border-bottom: none;
      padding-left: 0.75rem;
      padding-right: 0.75rem;
    }
  }
  .page-section-title {
    margin: rem(38) 0 1rem 0;
  }
  .col_company-info {
    width: 50%;
    padding-bottom: rem(24);
  }
  .col_company-financials,
  .col_company-info {
    border-right: 1px solid var(--color-background-low);
  }
  .col_company-financials,
  .col_company-score {
    width: 25%;
    padding-bottom: rem(24);
  }
}

@media (min-width: rem(1380)) {
  .paginator-links {
    justify-content: flex-end;
    gap: rem(16);
  }
  .paginator-link__text {
    display: none;
  }
  .paginator-link {
    position: fixed;
    top: 50%;
    margin-top: -24px;
    .icon {
      font-size: rem(48);
    }
  }
  .paginator-link--prev {
    left: 0;
  }
  .paginator-link--next {
    right: 0;
  }
}
