@font-face {
  font-family: 'icomoon';
  src:
    url('fonts/icomoon.ttf?e83can') format('truetype'),
    url('fonts/icomoon.woff?e83can') format('woff'),
    url('fonts/icomoon.svg?e83can#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

